  addItem(i: number, j: number, k: number) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    // console.log(data.items);
    data.dialog = Object.assign({}, data.dialog, {
      isShow: true,
      width: 720,
      persistent: true,
      type: 'add',
      title: '',
      inButton: [
        {
          label: ''
        },
        {
          label: '',
          type: 'add'
        }
      ],
      index: {
        i,
        j,
        k
      },
      editedItem: {
        avatar: {},
        name: '',
        phoneNumber: '',
        email: '',
        department: '',
        job: '',
        skills: [],
        address: ''
      }
    });
  }

  editItem(i: number, j: number, k: number, item: object) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    const index = data.items.indexOf(item);

    data.dialog = Object.assign({}, data.dialog, {
      isShow: true,
      width: 720,
      persistent: true,
      type: 'edit',
      title: '',
      text: '',
      inButton: [
