          type: 'edit'
        }
      ],
      index: {
        i,
        j,
        k
      },
      editedIndex: index,
      editedItem: Object.assign({}, item)
    });
  }

  deleteItem(i: number, j: number, k: number, item: object) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
