    // console.log(data);
    // confirm('Are you sure you want to delete this item?') && data.items.splice(index, 1);
  }

  delMultiItem(i: number, j: number, k: number, items: object) {
    // console.log(typeof items); // object
    // console.log(items.length);
    // console.log(items);

