            label: '',
            type: 'delMulti'
          }
        ],
        index: {
          i,
          j,
          k
        },
        delMultiItem: items
      });
    }
  }

  close(dialog: any) {
    const data = this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k];
    data.dialog.isShow = false;
    setTimeout(() => {
      data.dialog = Object.assign({}, data.dialog, {
        isShow: false,
        type: '',
        persistent: false,
        title: '',
        text: '',
        inButton: [],
        index: {},
        editedIndex: -1,
        editedItem: Object.assign({}, data.dialog.defaultItem),
        delMultiItem: []
      });
      setTimeout(() => {
        this.resetValidation();
      });
    }, 100);
  }

  dialog(action: string, dialog: any) {
    const data = this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k];
    const newItem = Object.assign({}, dialog.editedItem, {
      id: dialog.nextId,
      avatar: {
        icon: 'mdi-account',
        size: 32,
        color: 'borderColor4'
      }
    });
    switch (action) {
      case 'add':
        new Promise((resolve, reject) => {
          if (this.validate()) {
            resolve();
          }
        }).then(() => {
          data.items.push(newItem);
          dialog.nextId++;
          this.close(dialog);
        });
        break;

      case 'edit':
        if (this.validate()) {
          // data.items[dialog.editedIndex] = Object.assign({}, data.items[dialog.editedIndex], dialog.editedItem); // 
          // Object.assign(data.items[dialog.editedIndex], dialog.editedItem);
          this.$set(data.items, dialog.editedIndex, dialog.editedItem);

          // console.log(Object.assign({}, dialog.editedItem));
          // console.log(this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k].items[dialog.editedIndex]);

          this.close(dialog);
        }
        break;

      case 'del':
        data.items.splice(dialog.editedIndex, 1);
        data.selected = []; // 
        this.close(dialog);
        break;

      case 'delMulti':
        dialog.delMultiItem.forEach((element: any) => {
          const index = data.items.findIndex((value: any) => {
            return value.id === element.id;
          });
          if (index > -1) {
            data.items.splice(index, 1);
          }
        });
        data.selected = []; // 
        this.close(dialog);
        break;

      default:
        this.close(dialog);
    }
  }

  validate() {
    if (this.$refs.form[0].validate()) {
      // this.snackbar = true;
      return true;
    }
  }

  resetValidation() {
    this.$refs.form[0].resetValidation();
  }

  reset() {
    this.$refs.form.reset();
  }

  initDate(i: number, j: number, k: number) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    data.loading = true;
    setTimeout(() => {
      data.items = [].concat(data.initItems);
      data.loading = false;
    }, 1000);
    // console.log(data.items.length);
  }

  created() {
    const data = this.dataTablesItems[0].dataTables[0].items[0];
    data.loading = true;
    setTimeout(() => {
      data.items = [].concat(data.initItems);
      data.loading = false;
    }, 1000);
  }
